<template>
    <div
        :style="`position:absolute; top:0px; left:0px; width:100%; height:100%; transition:all 0.5s linear; display:flex; align-items:center; justify-content:center;`"
    >

        <template v-if="connState==='已連線' || connState==='連線中...'">

            <img style="width:100px; height:100px;" :src="img_connection">

            <div style="margin-left:10px;">
                <div style="color:#aaa;">
                    {{connState}}
                </div>
            </div>

        </template>

        <template v-else-if="connState==='已登出'">

            <img style="width:75px; height:75px;" :src="img_logout">

            <div style="margin-left:10px;">
                <div style="color:#aaa;">
                    {{connState}}
                </div>
            </div>

        </template>

        <template v-else-if="connState==='無法連線'">

            <img style="width:75px; height:75px;" :src="img_dissconnection">

            <div style="margin-left:10px;">
                <div style="color:#aaa;">
                    {{connState}}
                </div>
            </div>

        </template>

        <template v-else>

            <img style="width:75px; height:75px;" :src="img_deny">

            <div style="margin-left:10px;">
                <div style="color:#aaa;">
                    {{connState}}
                </div>
            </div>

        </template>

    </div>
</template>

<script>
import get from 'lodash-es/get.js'


export default {
    components: {
    },
    props: {
    },
    data: function() {
        return {
            img_connection: `data:image/svg+xml;base64,`,
            img_logout: `data:image/png;base64,`,
            img_dissconnection: `data:image/png;base64,`,
            img_deny: `data:image/png;base64,`,
        }
    },
    computed: {

        connState: function() {
            //console.log('computed loading')

            let vo = this

            return get(vo, `$store.state.connState`)
        },

    },
    methods: {
    }
}
</script>

<style scoped>
</style>
